import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Grid, Typography, Avatar } from "@mui/material";
import hoverLinkedin from "../../assets/linkedin-hover.webp";
import { TeamButton } from "../../utils/theme.js";
import { useMediaQuery } from "@mui/material";
import { theme } from "../../utils/theme.js";

const TeamWeb = (props) => {
  const { t } = useTranslation();

  const dimensionsLg = useMediaQuery(theme.breakpoints.down("lg"));
  const dimensionsXl = useMediaQuery(theme.breakpoints.down("xl"));

  const [linkedinHover, setLinkedinHover] = useState(null);

  const handleHoverIn = (name) => {
    setLinkedinHover(name);
  };

  const handleHoverOut = () => {
    setLinkedinHover(null);
  };

  return (
    <Grid
      container
      flexDirection="column"
      alignItems="center"
      pb={10}
      sx={{
        backgroundColor: "secondary.main",
        display: props.display,
      }}
    >
      <Typography variant="h2" mb={1.5} mt={15}>
        {t("teamComponent.title")}
      </Typography>
      <Typography variant="h4" textAlign="center" mb={8}>
        {t("teamComponent.subTitle")}
      </Typography>
      <Grid
        container
        item
        md={10}
        columnSpacing={4}
        rowSpacing={10}
        justifyContent="center"
      >
        {props.team.map((member) => (
          <Grid
            container
            item
            md={2.4}
            flexDirection="column"
            justifyContent="flex-start"
            alignItems="center"
            key={`member_${member.linkedin}`}
          >
            <Avatar
              style={{
                width: "fit-content",
                height: "fit-content",
              }}
            >
              <TeamButton
                onMouseMove={() => handleHoverIn(member.name)}
                onMouseLeave={handleHoverOut}
              >
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href={member.linkedin}
                >
                  {member.linkedin && linkedinHover === member.name ? (
                    <img
                      src={hoverLinkedin}
                      style={{
                        width: dimensionsLg
                          ? "152px"
                          : dimensionsXl
                          ? "200px"
                          : "250px",
                      }}
                      height="auto"
                      alt="HovertoLinkedin"
                    ></img>
                  ) : (
                    <img
                      src={member.image}
                      style={{
                        width: dimensionsLg
                          ? "152px"
                          : dimensionsXl
                          ? "200px"
                          : "250px",
                      }}
                      height="auto"
                      alt={member.name}
                    ></img>
                  )}
                </a>
              </TeamButton>
            </Avatar>
            <Grid item textAlign="center" mt={2.5}>
              <Typography variant="h4Bold">{member.name}</Typography>
              <Typography
                variant="h5"
                textAlign="center"
                color="#353945"
                mt={0.7}
              >
                {member.title}
              </Typography>
            </Grid>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};
export default TeamWeb;
